}
.menu li:nth-child(3) {
  transition-delay: 400ms;
}
.menu li:nth-child(4) {
  transition-delay: 500ms;
}
.menu a {
  text-transform: uppercase;
  color: #fff;
  font-family: "UniSansHeavyCaps";
  font-size: 19px;
  letter-spacing: 0.2em;
  position: relative;
  left: 0;
  opacity: 1;
}
.menu a span {
  opacity: 1;
  left: 0;
  position: relative;
  pointer-events: none;
  transition: all .3s linear;
}
.menu a:before {
  content: attr(data-hover);
  text-align: center;
  position: absolute;
  pointer-events: none;
  top: 0;
  left: -40px;
  opacity: 0;
  color: inherit;
  font-size: inherit;
  letter-spacing: inherit;
  transition: all .3s linear;
}
.menu a:hover {
  text-decoration: none;
}
.menu a:hover span {
  opacity: 0;
  left: 40px;
}
.menu a:hover:before {
  opacity: 1;
  left: 0;
}
.menu .background {
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(37, 37, 37, 0.9);
  width: calc(100% - 0px);
  height: calc(100% - 0px);
  transition: all 500ms 500ms;
}
.menu.open {
  visibility: visible;
}
.menu.open .contacts {
  transform: translateX(0);
  transition: all 500ms 750ms;
}
