  left: 3px;
}
.lang-switcher .active + .pointer {
  left: 46px;
}
@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  transition: z-index 10ms 2000ms;
}
.preloader > div {
  position: fixed;
  background-color: #252525;
}
.preloader.active {
  z-index: 9999;
  transition: z-index 0ms 0ms;
}
.preloader.active > div {
  width: 100%;
  height: 100%;
  transition-delay: 0ms;
}
.preloader.active .progress {
  transition: all 10ms 0ms;
  height: 100%;
}
.preloader.active .progress svg {
  transition: transform 2000ms;
}
.preloader.active .progress svg:nth-child(1) {
  transform: rotate(0deg) translateY(100%);
}
.preloader.active .progress svg:nth-child(2) {
  transform: rotate(60deg) translateY(100%);
}
.preloader.active .progress svg:nth-child(3) {
  transform: rotate(120deg) translateY(100%);
}
.preloader.active .progress svg:nth-child(4) {
  transform: rotate(180deg) translateY(100%);
}
.preloader.active .progress svg:nth-child(5) {
  transform: rotate(240deg) translateY(100%);
}
.preloader.active .progress svg:nth-child(6) {
  transform: rotate(300deg) translateY(100%);
}
.preloader__left {
  top: 0;
  left: 0;
  height: 100%;
  width: 0%;
  transition: width 1000ms 1000ms;
}
.preloader__right {
  top: 0;
  right: 0;
  height: 100%;
  width: 0%;
  transition: width 1000ms 1000ms;
}
.preloader__top {
  top: 0;
  left: 0;
  width: 100%;
  height: 0%;
  transition: height 1000ms 1000ms;
}
.preloader__bottom {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0%;
  transition: height 1000ms 1000ms;
}
.preloader .progress {
  animation: rotating 5000ms linear infinite;
  width: 100%;
  height: 0;
  background-color: transparent;
  transition: all 10ms 1500ms;
}
.preloader .progress svg {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  transform-origin: 50% 50%;
  transition: transform 5000ms;
}
