    height: auto;
    width: 100%;
    min-height: initial;
    margin-bottom: 20px;
  }
}
.fullheight-image {
  max-height: 100vh;
  min-height: 700px;
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}
@media screen and (max-width: 1024px) {
  .fullheight-image {
    min-height: 100vh;
    max-height: 100vh;
  }
}
.fullheight-image h2 {
  margin: 0;
}
.fullheight-image .slogan {
  color: #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  text-align: center;
  width: 100%;
  height: 32px;
}
.slogan-block {
  padding: 100px 10px;
  text-align: center;
}
.slogan-block .slogan {
  margin: auto;
  max-width: 66.66666667%;
  font-size: 24px;
}
@media screen and (max-width: 1024px) {
  .project-tabs h2 {
    padding-left: 15px;
  }
}
@media screen and (max-width: 1024px) {
  .project-tabs {
    padding-left: 0;
    padding-right: 0;
  }
}
.list-info ul {
  list-style-image: url("../img/svg/triangle.svg");
}
.list-info li {
  margin-bottom: 20px;
}
.popup {
  position: fixed;
  top: -200%;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  z-index: 1000;
  transition: top 1ms 600ms, opacity 300ms 300ms;
}
.popup.active {
  top: 0;
  opacity: 1;
  transition: top 1ms 0ms, opacity 300ms 0ms;
}
.popup > div {
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 462px;
  height: 348px;
  padding: 50px 70px;
  background-color: #fff;
  border-radius: 25px;
}
@media screen and (max-width: 1024px) {
  .popup > div {
    width: 100%;
    height: auto;
    max-height: 100vh;
    top: 0;
    bottom: auto;
    border-radius: 0;
    padding: 40px 20px;
  }
}
.popup .popup-close {
  position: fixed;
  right: 10px;
  top: 10px;
  width: 19px;
  height: 19px;
  cursor: pointer;
}
.popup .popup-close .bar {
  position: relative;
  width: 21px;
  height: 1px;
  background-color: #252525;
  transform-origin: 50% 100%;
  transform: rotate(45deg);
  top: 7px;
}
.popup .popup-close .bar:last-child {
  transform: rotate(-45deg);
  left: 1px;
  top: 6px;
}
.popup--side {
  position: fixed;
}
.popup--side.active > div {
  right: 0;
  transition: all 500ms 500ms;
}
.popup--side > div {
  position: fixed;
  top: 0;
  bottom: 0;
  right: -100%;
  left: initial;
  width: 66%;
  height: 100%;
  padding: 40px;
  background-color: #fff;
  transition: all 500ms 0ms;
  border-radius: 0px;
  overflow: auto;
  text-align: left;
}
@media screen and (max-width: 1024px) {
  .popup--side > div {
    width: 100%;
    height: auto;
    top: 0;
    bottom: auto;
    border-radius: 0;
    padding: 40px 20px;
  }
}
